import { createRouter, createWebHistory } from 'vue-router';
import store from '@/store'; // Vuex store import
import LayoutComponent from '@/components/LayoutComponent.vue';  // Layout 경로 및 이름 수정
import HomePage from '@/views/HomePage.vue';
import CeoGreeting from '@/views/CeoGreeting.vue';
import OrganizationChartPage from '@/views/OrganizationChartPage.vue';
import ConsultantPage from '@/views/ConsultantPage.vue';
import ContactPage from '@/views/ContactPage.vue';
import HistoryPage from '@/views/HistoryPage.vue';
import LocationPage from '@/views/LocationPage.vue';

import OpenPosition from '@/views/OpenPosition.vue';
import OpenPositionDetail from '@/views/OpenPositionDetail.vue';  // 포지션 상세 페이지 추가
import OpenPositionForm from '@/views/OpenPositionForm.vue';

import LoginHomePage from '@/views/LoginHomePage.vue';
import LoginSignupPage from '@/views/LoginSignupPage.vue';
import LoginForgotPasswordPage from '@/views/LoginForgotPasswordPage.vue';

import SeminarArchive from '@/views/SeminarArchive.vue';
import SeminarDetail from '@/views/SeminarDetail.vue';  // 세미나 상세 페이지 추가
import SeminarForm from '@/views/SeminarForm.vue';  // 세미나 등록/수정 페이지 추가

import TalentArchive from '@/views/TalentArchive.vue';
import TalentDetail from '@/views/TalentDetail.vue';
import TalentForm from '@/views/TalentForm.vue';

const routes = [
  {
    path: '/',
    component: LayoutComponent,  // LayoutComponent 적용
    children: [
      { path: '', component: HomePage },
      { path: '/ceo-greeting', component: CeoGreeting },
      { path: '/organization-chart', component: OrganizationChartPage },
      { path: '/consultant', component: ConsultantPage },
      { path: '/contact', component: ContactPage },
      { path: '/history', component: HistoryPage },
      { path: '/location', component: LocationPage },

      // open position 관련
      { path: '/open-position', component: OpenPosition }, // 포지션 목록
      { path: '/open-position/create', name: 'CreatePosition', component: OpenPositionForm, meta: { requiresAuth: true } }, // 새 포지션 생성
      { path: '/open-position/:id', name: 'OpenPositionDetail', component: OpenPositionDetail, meta: { requiresAuth: true } }, // 포지션 상세 및 수정
      { path: '/open-position/:id/edit', name: 'EditPosition', component: OpenPositionForm, meta: { requiresAuth: true } }, // 기존 포지션 수정

      // login관련
      { path: '/login', name: 'Login', component: LoginHomePage },
      { path: '/signup', component: LoginSignupPage },
      { path: '/forgot-password', component: LoginForgotPasswordPage },

      // seminar 관련
      { path: '/seminar-archive', name: 'SeminarArchive', component: SeminarArchive }, // 세미나 목록
      { path: '/seminar/:id', name: 'SeminarDetail', component: SeminarDetail }, // 세미나 상세
      { path: '/seminar/create', name: 'CreateSeminar', component: SeminarForm, meta: { requiresAuth: true } }, // 세미나 생성
      { path: '/seminar/:id/edit', name: 'EditSeminar', component: SeminarForm, meta: { requiresAuth: true } }, // 세미나 수정

      // talent 관련
      { path: '/talent-database', name: 'TalentArchive', component: TalentArchive, meta: { requiresAuth: true } },
      { path: '/talent-database/create', name: 'CreateTalent', component: TalentForm, meta: { requiresAuth: true } },
      { path: '/talent-database/:id', name: 'TalentDetail', component: TalentDetail, meta: { requiresAuth: true } },
      { path: '/talent-database/:id/edit', name: 'EditTalent', component: TalentForm, meta: { requiresAuth: true } }
    ]
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

// 네비게이션 가드 추가
router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth && !store.getters.isLoggedIn) {
    // 인증이 필요한 페이지인데 로그인되지 않은 경우 로그인 페이지로 리다이렉트
    next({ name: 'Login' });
  } else {
    next(); // 인증이 필요하지 않거나 로그인된 경우 그대로 진행
  }
});

export default router;
