import { createApp } from 'vue';
import App from './App.vue';
import store from './store'; // Vuex store import
import router from './router';
import '@/assets/css/styles.css';  // 전역으로 CSS 불러오기
import 'bootstrap/dist/css/bootstrap.min.css';  // Bootstrap CSS 추가
import 'bootstrap';  // Bootstrap JS 추가
import axios from 'axios'; // axios import 추가

// axios 전역 설정: 쿠키를 포함시키기 위해 withCredentials 옵션 활성화
axios.defaults.withCredentials = true; // 모든 요청에 쿠키 포함

// axios를 전역에서 사용할 수 있도록 Vue에 플러그인으로 추가
const app = createApp(App);

// Vue 인스턴스에 Axios를 설정하여 모든 컴포넌트에서 사용 가능하도록 설정
app.config.globalProperties.$axios = axios;  // Vue 인스턴스의 전역 속성으로 axios를 추가

// Vue Router, Vuex Store 추가
app.use(router)
  .use(store)
  .mount('#app');

// 이제 모든 컴포넌트에서 `this.$axios`로 axios에 접근할 수 있습니다.