<template>
  <div class="container mt-5">
    <div class="card">
      <div class="card-header">
        <h2>{{ talent.name }} - {{ talent.position }}</h2>
      </div>
      <div class="card-body">
        <ul class="list-group list-group-flush">
          <li class="list-group-item">
            <strong>생년:</strong> {{ talent.birthYear }}
          </li>
          <li class="list-group-item">
            <strong>전화번호:</strong> {{ talent.phone }}
          </li>
          <li class="list-group-item">
            <strong>이메일:</strong> {{ talent.email }}
          </li>
          <li class="list-group-item">
            <strong>학교:</strong> {{ talent.school }}
          </li>
          <li class="list-group-item">
            <strong>경력:</strong> {{ talent.experience }}
          </li>
          <li class="list-group-item">
            <strong>전문 분야:</strong> {{ talent.expertise }}
          </li>
          <li class="list-group-item">
            <strong>진행 사항:</strong> {{ talent.status }}
          </li>
          <li class="list-group-item">
            <strong>입수 경로:</strong> {{ talent.source }}
          </li>
          <li class="list-group-item">
            <strong>이력서 번호:</strong> {{ talent.resumeNumber }}
          </li>
          <li class="list-group-item">
            <strong>컨택 담당자:</strong> {{ talent.contactPerson }}
          </li>
          <li class="list-group-item">
            <strong>업무 내용:</strong> {{ talent.jobDescription }}
          </li>
          <li class="list-group-item">
            <strong>자격 요건:</strong> {{ talent.qualifications }}
          </li>
          <li class="list-group-item">
            <strong>관련 전공 및 경력:</strong> {{ talent.majorAndCareer }}
          </li>
        </ul>

        <!-- 버튼 그룹 -->
        <div class="d-flex justify-content-start mt-4">
          <!-- 목록으로 돌아가기 버튼 -->
          <router-link to="/talent-database" class="btn btn-primary me-2">
            목록으로 돌아가기
          </router-link>

          <!-- 수정 버튼 -->
          <button @click="handleEditTalent" class="btn btn-warning ms-3">
            수정
          </button>

          <!-- 삭제 버튼 -->
          <button @click="handleDeleteTalent" class="btn btn-danger ms-3">
            삭제
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: "TalentDetail",
  data() {
    return {
      talent: {}, // 선택된 인재 정보를 저장
    };
  },
  computed: {
    ...mapGetters(['isLoggedIn']), // Vuex 스토어에서 로그인 상태 가져오기
  },
  mounted() {
    // 라우터에서 전달된 ID로 인재 정보 불러오기
    const talentId = this.$route.params.id;
    this.fetchTalentDetail(talentId);
  },
  methods: {
    // 인재 상세 정보 불러오기
    async fetchTalentDetail(talentId) {
      try {
        const response = await fetch(`/api/talents/${talentId}`);
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        const data = await response.json();
        this.talent = data; // 데이터 할당
      } catch (error) {
        console.error('Error fetching talent detail:', error);
      }
    },

    // 인재 삭제 기능
    async handleDeleteTalent() {
      if (!this.isLoggedIn) {
        alert('로그인이 필요합니다. 로그인 페이지로 이동합니다.');
        this.$router.push({ name: 'Login' });
        return;
      }

      const confirmed = confirm('정말로 이 인재를 삭제하시겠습니까?');
      if (!confirmed) return;

      try {
        const response = await fetch(`/api/talents/${this.$route.params.id}`, {
          method: 'DELETE',
        });

        if (!response.ok) {
          throw new Error('Network response was not ok');
        }

        alert('인재가 삭제되었습니다.');
        this.$router.push('/talent-database');
      } catch (error) {
        console.error('Error deleting talent:', error);
        alert('인재 삭제에 실패했습니다.');
      }
    },

    // 인재 수정 기능
    handleEditTalent() {
      if (!this.isLoggedIn) {
        alert('로그인이 필요합니다. 로그인 페이지로 이동합니다.');
        this.$router.push({ name: 'Login' });
        return;
      }

      // 로그인된 경우 수정 페이지로 이동
      this.$router.push({ name: 'EditTalent', params: { id: this.talent.id } });
    }
  }
};
</script>

