<template>
  <div class="signup-page container">
    <h2>회원가입</h2>
    <form @submit.prevent="signup">
      <!-- Name input -->
      <div class="form-group">
        <label for="name">이름</label>
        <input type="text" id="name" v-model="name" class="form-control" required placeholder="이름을 입력하세요" />
      </div>

      <!-- Email input with domain validation -->
      <div class="form-group">
        <label for="email">이메일</label>
        <div class="d-flex">
          <input type="email" id="email" v-model="email" class="form-control" required placeholder="이메일을 입력하세요"
            @blur="validateEmail" />
          <button type="button" :disabled="!emailValid || emailChecked" @click="checkEmailExists"
            class="btn btn-secondary ml-2">중복 확인</button>
        </div>
        <span v-if="emailError">{{ emailError }}</span>
        <span v-if="emailValid && emailChecked && !emailError">사용 가능한 이메일입니다.</span>
      </div>

      <!-- Password input -->
      <div class="form-group">
        <label for="password">비밀번호</label>
        <input type="password" id="password" v-model="password" class="form-control" required minlength="8"
          placeholder="비밀번호를 8자 이상 입력하세요" />
      </div>

      <!-- Verification code section -->
      <div class="form-group">
        <button type="button" :disabled="!emailValid || !emailChecked" @click="sendVerificationCode"
          class="btn btn-secondary">인증 코드 발송</button>
      </div>

      <div v-if="verificationCodeSent" class="form-group">
        <label for="verificationCode">인증 코드</label>
        <div class="d-flex">
          <input type="text" id="verificationCode" v-model="verificationCode" class="form-control" required
            placeholder="이메일로 받은 인증 코드를 입력하세요" />
          <button type="button" @click="verifyCode" class="btn btn-secondary ml-2">인증 코드 검증</button>
        </div>
      </div>

      <!-- Submit button -->
      <div class="form-group">
        <button type="submit" class="btn btn-primary">회원가입</button>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      name: '',
      email: '',
      password: '',
      verificationCode: '',
      isVerified: false,
      emailValid: false,
      emailError: '',
      verificationCodeSent: false,
      emailChecked: false
    };
  },
  methods: {
    async validateEmail() {
      const allowedDomains = ['union-search.com', 'yonsei.ac.kr'];
      if (this.email) {
        const domain = this.email.split('@')[1];
        if (!allowedDomains.includes(domain)) {
          this.emailValid = false;
          this.emailError = '허용된 이메일 도메인만 사용할 수 있습니다.';
        } else {
          this.emailValid = true;
          this.emailError = '';
          this.emailChecked = false;
        }
      } else {
        this.emailValid = false;
        this.emailError = '이메일을 입력해 주세요.';
      }
    },
    async checkEmailExists() {
      try {
        const response = await axios.get('/api/auth/check-email', { params: { email: this.email } });

        // 서버에서 받은 응답에 따라 상태 업데이트
        if (response.data.status === 'success') {
          if (response.data.exists) {
            this.emailValid = false;
            this.emailError = '이미 사용 중인 이메일입니다.';
          } else {
            this.emailValid = true;
            this.emailError = '';
            this.emailChecked = true;
          }
        } else {
          this.emailValid = false;
          this.emailError = '이메일 확인 중 오류가 발생했습니다.';
        }
      } catch (error) {
        this.emailValid = false;
        this.emailError = '이메일 확인 중 오류가 발생했습니다.';
      }
    },
    async sendVerificationCode() {
      if (this.emailValid && this.emailChecked) {
        try {
          alert('인증 코드 전송을 시작합니다.');
          const response = await axios.post('/api/auth/send-verification-code', null, {
            params: { email: this.email }
          });

          if (response.data.status === 'success') {
            alert('인증 코드가 전송되었습니다.');
            this.verificationCodeSent = true;
          } else {
            alert(response.data.message);
          }
        } catch (error) {
          if (error.response && error.response.data && error.response.data.message) {
            alert(error.response.data.message);
          } else {
            alert('인증 코드 전송에 실패했습니다.');
          }
        }
      } else {
        alert('이메일 중복 확인을 먼저 진행해 주세요.');
      }
    },
    async verifyCode() {
      if (!this.email || !this.verificationCode) {
        alert('이메일과 인증 코드를 모두 입력해 주세요.');
        return;
      }

      try {
        const response = await axios.post('/api/auth/verify-code', {
          email: this.email,
          code: this.verificationCode
        });
        if (response.data.status === 'success') {
          this.isVerified = true;
          alert('인증이 완료되었습니다.');
        } else {
          alert(response.data.message || '인증 코드가 잘못되었습니다.');
        }
      } catch (error) {
        if (error.response && error.response.data && error.response.data.message) {
          alert(error.response.data.message);
        } else {
          alert('인증 검증 중 오류가 발생했습니다.');
        }
      }
    },
    async signup() {
      if (!this.emailChecked) {
        alert('이메일 중복 확인을 먼저 진행해 주세요.');
        return;
      }
      if (!this.isVerified) {
        alert('인증 코드가 확인되지 않았습니다.');
        return;
      }

      // 회원가입 로직
      try {
        const signupData = {
          email: this.email,
          name: this.name,
          password: this.password,
        };

        // 서버에 회원가입 요청
        const response = await axios.post('/api/auth/signup', signupData);

        if (response.data.status === 'success') {
          alert(response.data.message);
          // 회원가입 성공 시 이전 페이지로 이동
          this.$router.push('/login');
        } else {
          alert(response.data.message);
        }
      } catch (error) {
        if (error.response && error.response.data && error.response.data.message) {
          alert(error.response.data.message);
        } else {
          alert('회원가입 중 오류가 발생했습니다.');
        }
      }
    }
  }
};
</script>