<template>
  <div class="forgot-password-page container">
    <h2>비밀번호 찾기</h2>
    <form @submit.prevent="resetPassword">
      <div class="form-group">
        <label for="email">이메일</label>
        <input type="email" id="email" v-model="email" class="form-control" required placeholder="이메일을 입력하세요" />
      </div>
      <div class="form-group">
        <button type="submit" class="btn btn-primary">비밀번호 재설정</button>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      email: ''
    };
  },
  methods: {
    async resetPassword() {
      try {
        await axios.post('/api/forgot-password', {
          email: this.email
        });
        alert('임시 비밀번호가 이메일로 발송되었습니다.');
        this.$router.push('/login');
      } catch (error) {
        if (error.response && error.response.data) {
          alert(`비밀번호 재설정 실패: ${error.response.data}`);
        } else {
          alert('비밀번호 재설정 실패: 서버에 문제가 발생했습니다.');
        }
      }
    }
  }
};
</script>
