<template>
  <section>
    <div class="container mt-5">
      <h1 class="text-start mb-5">{{ isEditMode ? '인재 정보 수정' : '새 인재 등록' }}</h1>

      <!-- 인재 등록/수정 폼 -->
      <form @submit.prevent="submitTalent">
        <div class="mb-3">
          <label for="date" class="form-label">일시</label>
          <input type="date" id="date" v-model="talent.date" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="position" class="form-label">포지션</label>
          <input type="text" id="position" v-model="talent.position" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="name" class="form-label">성명</label>
          <input type="text" id="name" v-model="talent.name" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="birthYear" class="form-label">생년</label>
          <input type="text" id="birthYear" v-model="talent.birthYear" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="phone" class="form-label">전화번호</label>
          <input type="text" id="phone" v-model="talent.phone" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="email" class="form-label">이메일</label>
          <input type="email" id="email" v-model="talent.email" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="school" class="form-label">학교</label>
          <input type="text" id="school" v-model="talent.school" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="experience" class="form-label">경력</label>
          <textarea id="experience" v-model="talent.experience" class="form-control" rows="3" required></textarea>
        </div>

        <div class="mb-3">
          <label for="expertise" class="form-label">전문분야</label>
          <textarea id="expertise" v-model="talent.expertise" class="form-control" rows="3" required></textarea>
        </div>

        <div class="mb-3">
          <label for="status" class="form-label">진행사항</label>
          <input type="text" id="status" v-model="talent.status" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="source" class="form-label">입수 경로</label>
          <input type="text" id="source" v-model="talent.source" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="resumeNumber" class="form-label">이력서 번호</label>
          <input type="text" id="resumeNumber" v-model="talent.resumeNumber" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="contactPerson" class="form-label">컨택 담당자</label>
          <input type="text" id="contactPerson" v-model="talent.contactPerson" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="jobDescription" class="form-label">업무내용</label>
          <textarea id="jobDescription" v-model="talent.jobDescription" class="form-control" rows="3"
            required></textarea>
        </div>

        <div class="mb-3">
          <label for="qualifications" class="form-label">자격 요건</label>
          <textarea id="qualifications" v-model="talent.qualifications" class="form-control" rows="3"
            required></textarea>
        </div>

        <div class="mb-3">
          <label for="majorAndCareer" class="form-label">관련 전공 및 경력</label>
          <textarea id="majorAndCareer" v-model="talent.majorAndCareer" class="form-control" rows="3"
            required></textarea>
        </div>

        <button type="submit" class="btn btn-primary">{{ isEditMode ? '수정 완료' : '등록' }}</button>
      </form>
    </div>
  </section>
</template>

<script>
export default {
  name: "CreateTalent",
  data() {
    return {
      talent: {
        date: '',             // 일시
        position: '',         // 포지션
        name: '',             // 성명
        birthYear: '',        // 생년
        phone: '',            // 전화번호
        email: '',            // 이메일
        school: '',           // 학교
        experience: '',       // 경력
        expertise: '',        // 전문분야
        status: '',           // 진행사항
        source: '',           // 입수경로
        resumeNumber: '',     // 이력서 번호
        contactPerson: '',    // 컨택 담당자
        jobDescription: '',   // 업무 내용
        qualifications: '',   // 자격 요건
        majorAndCareer: ''    // 관련 전공 및 경력
      },
      isEditMode: false // 수정 모드 여부
    };
  },
  methods: {
    async fetchTalentDetails(id) {
      try {
        const response = await fetch(`/api/talents/${id}`);
        if (!response.ok) {
          throw new Error('인재 정보를 불러오는데 실패했습니다.');
        }
        const data = await response.json();
        this.talent = data; // 기존 인재 데이터 폼에 채움
      } catch (error) {
        console.error('Error fetching talent details:', error);
      }
    },
    async submitTalent() {
      try {
        const url = this.isEditMode ? `/api/talents/${this.$route.params.id}` : '/api/talents';
        const method = this.isEditMode ? 'PUT' : 'POST';

        const response = await fetch(url, {
          method: method,
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(this.talent)
        });

        if (!response.ok) {
          throw new Error('네트워크 응답에 문제가 발생했습니다.');
        }

        this.$router.push('/talent-database'); // 등록/수정 후 목록 페이지로 이동
      } catch (error) {
        console.error('Error submitting talent:', error);
      }
    }
  },
  mounted() {
    const talentId = this.$route.params.id;
    if (talentId) {
      // talentId가 있는 경우 수정 모드로 전환
      this.isEditMode = true;
      this.fetchTalentDetails(talentId); // 기존 인재 정보를 가져와 폼에 미리 채움
    }
  }
};
</script>

<style scoped>
