<template>
  <div class="container mt-5">
    <div class="card">
      <div class="card-header">
        <h2>{{ seminar.title }}</h2>
      </div>
      <div class="card-body">
        <ul class="list-group list-group-flush">
          <li class="list-group-item">
            <strong>발표일:</strong> {{ seminar.presentationDate }}
          </li>
          <li class="list-group-item">
            <strong>장소:</strong> {{ seminar.location }}
          </li>
          <li class="list-group-item">
            <strong>발표자:</strong> {{ seminar.presenter }}
          </li>
          <li class="list-group-item">
            <strong>등록자:</strong> {{ seminar.registrar }}
          </li>
          <li class="list-group-item">
            <strong>세미나 날짜:</strong> {{ seminar.date }}
          </li>
        </ul>

        <!-- 버튼 그룹 -->
        <div class="d-flex justify-content-start mt-4">
          <!-- 목록으로 돌아가기 버튼 -->
          <router-link to="/seminar-archive" class="btn btn-primary me-2">
            목록으로 돌아가기
          </router-link>

          <!-- 수정 버튼 -->
          <button @click="handleEditSeminar" class="btn btn-warning ms-3">
            수정
          </button>

          <!-- 삭제 버튼 -->
          <button @click="handleDeleteSeminar" class="btn btn-danger ms-3">
            삭제
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: "SeminarDetail",
  data() {
    return {
      seminar: {}, // 선택된 세미나 정보를 저장
    };
  },
  computed: {
    ...mapGetters(['isLoggedIn']), // Vuex 스토어에서 로그인 상태 가져오기
  },
  mounted() {
    // 라우터에서 전달된 ID로 세미나 정보 불러오기
    const seminarId = this.$route.params.id;
    this.fetchSeminarDetail(seminarId);
  },
  methods: {
    // 세미나 상세 정보 불러오기
    async fetchSeminarDetail(seminarId) {
      try {
        const response = await fetch(`/api/seminars/${seminarId}`);
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        const data = await response.json();
        this.seminar = data; // 데이터 할당
      } catch (error) {
        console.error('Error fetching seminar detail:', error);
      }
    },

    // 세미나 삭제 기능
    async handleDeleteSeminar() {
      if (!this.isLoggedIn) {
        alert('로그인이 필요합니다. 로그인 페이지로 이동합니다.');
        this.$router.push({ name: 'Login' });
        return;
      }

      const confirmed = confirm('정말로 이 세미나를 삭제하시겠습니까?');
      if (!confirmed) return;

      try {
        const response = await fetch(`/api/seminars/${this.$route.params.id}`, {
          method: 'DELETE',
        });

        if (!response.ok) {
          throw new Error('Network response was not ok');
        }

        alert('세미나가 삭제되었습니다.');
        this.$router.push('/seminar-archive');
      } catch (error) {
        console.error('Error deleting seminar:', error);
        alert('세미나 삭제에 실패했습니다.');
      }
    },

    // 세미나 수정 기능
    handleEditSeminar() {
      if (!this.isLoggedIn) {
        alert('로그인이 필요합니다. 로그인 페이지로 이동합니다.');
        this.$router.push({ name: 'Login' });
        return;
      }

      // 로그인된 경우 수정 페이지로 이동
      this.$router.push({ name: 'EditSeminar', params: { id: this.seminar.id } });
    }
  }
};
</script>

