<template>
  <section>
    <div class="container mt-5">
      <!-- 수정 모드인지 새 등록 모드인지에 따라 제목이 달라짐 -->
      <h1 class="text-start mb-5">{{ isEditMode ? '세미나 수정' : '새 세미나 등록' }}</h1>

      <!-- 세미나 등록/수정 폼 -->
      <form @submit.prevent="submitSeminar">
        <div class="mb-3">
          <label for="title" class="form-label">세미나 제목</label>
          <input type="text" id="title" v-model="seminar.title" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="date" class="form-label">세미나 날짜</label>
          <input type="date" id="date" v-model="seminar.date" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="presentationDate" class="form-label">발표일</label>
          <input type="date" id="presentationDate" v-model="seminar.presentationDate" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="location" class="form-label">장소</label>
          <input type="text" id="location" v-model="seminar.location" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="presenter" class="form-label">발표자</label>
          <input type="text" id="presenter" v-model="seminar.presenter" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="registrar" class="form-label">등록자</label>
          <input type="text" id="registrar" v-model="seminar.registrar" class="form-control" required>
        </div>

        <button type="submit" class="btn btn-primary">{{ isEditMode ? '수정 완료' : '등록' }}</button>
      </form>
    </div>
  </section>
</template>

<script>
export default {
  name: "CreateSeminar",
  data() {
    return {
      seminar: {
        title: '',
        date: '',
        presentationDate: '',
        location: '',
        presenter: '',
        registrar: ''
      },
      isEditMode: false // 수정 모드 여부
    };
  },
  methods: {
    async fetchSeminarDetails(id) {
      try {
        const response = await fetch(`/api/seminars/${id}`);
        if (!response.ok) {
          throw new Error('세미나 정보를 불러오는데 실패했습니다.');
        }
        const data = await response.json();
        this.seminar = data; // 기존 세미나 데이터를 폼에 미리 채움
      } catch (error) {
        console.error('Error fetching seminar details:', error);
      }
    },
    async submitSeminar() {
      try {
        const url = this.isEditMode ? `/api/seminars/${this.$route.params.id}` : '/api/seminars';
        const method = this.isEditMode ? 'PUT' : 'POST';

        const response = await fetch(url, {
          method: method,
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(this.seminar)
        });

        if (!response.ok) {
          throw new Error('네트워크 응답에 문제가 발생했습니다.');
        }

        // 등록/수정 완료 후 세미나 목록 페이지로 이동
        this.$router.push('/seminar-archive');
      } catch (error) {
        console.error('Error submitting seminar:', error);
      }
    }
  },
  mounted() {
    const seminarId = this.$route.params.id;
    if (seminarId) {
      // seminarId가 있는 경우 수정 모드로 전환
      this.isEditMode = true;
      this.fetchSeminarDetails(seminarId); // 기존 세미나 정보를 가져와 폼에 미리 채움
    }
  }
};
</script>

<style scoped>
