import { createStore } from 'vuex';
import axios from 'axios';

// axios 전역 설정: 로그인 이후의 요청에 대해서만 쿠키를 포함시키기 위해 설정
axios.defaults.withCredentials = true;

export default createStore({
  state: {
    isLoggedIn: false,
    userName: ''
  },
  mutations: {
    SET_LOGIN_STATUS(state, payload) {
      state.isLoggedIn = payload.isLoggedIn;
      state.userName = payload.userName;
    }
  },
  actions: {
    async login({ commit }, credentials) {
      try {
        // 로그인 요청 시에도 withCredentials 옵션을 사용하도록 설정
        const response = await axios.post('/api/auth/login', credentials, { withCredentials: true });
        if (response.status === 200 && response.data?.username) {
          commit('SET_LOGIN_STATUS', {
            isLoggedIn: true,
            userName: response.data.username
          });
          return { success: true };
        } else {
          return { success: false, message: response.data.message || '로그인 실패' };
        }
      } catch (error) {
        return { success: false, message: "로그인에 실패하였습니다. 이메일과 비밀번호를 확인해 주세요." };
      }
    },

    async logout({ commit }) {
      try {
        await axios.post('/api/auth/logout', {}, { withCredentials: true });
        commit('SET_LOGIN_STATUS', {
          isLoggedIn: false,
          userName: ''
        });
        return { success: true };
      } catch (error) {
        alert('로그아웃에 실패했습니다. 다시 시도해주세요.');
        return { success: false };
      }
    },

    async checkLoginStatus({ commit }) {
      try {
        // 로그인 상태 체크 시에는 withCredentials 사용 (기본 설정)
        const response = await axios.get('/api/auth/check-login', { withCredentials: true });
        if (response.status === 200 && response.data.isLoggedIn) {
          commit('SET_LOGIN_STATUS', {
            isLoggedIn: true,
            userName: response.data.username
          });
        } else {
          commit('SET_LOGIN_STATUS', {
            isLoggedIn: false,
            userName: ''
          });
        }
      } catch (error) {
        commit('SET_LOGIN_STATUS', {
          isLoggedIn: false,
          userName: ''
        });
      }
    }
  },
  getters: {
    isLoggedIn: state => state.isLoggedIn,
    userName: state => state.userName
  }
});
