<template>
  <section>
    <div class="container mt-5">
      <h1 class="text-start mb-5">오픈 포지션</h1>

      <!-- 검색 바 -->
      <div class="input-group search-bar-container mb-3">
        <input type="text" v-model="searchQuery" class="form-control" placeholder="검색어 입력">
        <button class="btn btn-primary" @click="performSearch">검색</button>
      </div>

      <!-- 항목 수 선택 드롭다운 -->
      <div class="d-flex justify-content-end mb-3">
        <label for="itemsPerPage" class="me-2">페이지 당 항목 수:</label>
        <select id="itemsPerPage" v-model="itemsPerPage" @change="changeItemsPerPage" class="form-select w-auto">
          <option value="10">10</option>
          <option value="30">30</option>
          <option value="50">50</option>
          <option value="100">100</option>
        </select>
      </div>

      <!-- 포지션 목록 테이블 -->
      <table class="table custom-table table-hover">
        <thead>
          <tr>
            <th scope="col">detail</th>
            <th scope="col">포지션명</th>
            <th scope="col">회사</th>
            <th scope="col">직급</th>
            <th scope="col">상태</th>
            <th scope="col">등록일</th>
          </tr>
        </thead>
        <tbody>
          <!-- 검색 및 페이징 처리된 포지션 리스트 -->
          <tr v-for="position in paginatedPositions" :key="position.id" @click="goToDetail(position.id)">
            <td>{{ position.id }}</td>
            <td>{{ position.title }}</td>
            <td>{{ position.company }}</td>
            <td>{{ position.level }}</td>
            <td>{{ position.status }}</td>
            <td>{{ position.date }}</td>
          </tr>
        </tbody>
      </table>

      <!-- 데이터가 없을 때 보여줄 메시지 -->
      <div v-if="(isSearching && filteredPositions.length === 0)">
        <p>검색 결과가 없습니다. 다른 검색어를 시도해 주세요.</p>
      </div>

      <!-- 로딩 중일 때 보여줄 메시지 -->
      <div v-if="isLoading">
        <p>로딩 중... 잠시 기다려 주세요.</p>
      </div>

      <!-- 페이지네이션 -->
      <nav v-if="totalPages > 1" aria-label="Page navigation">
        <ul class="pagination justify-content-center">
          <li class="page-item" :class="{ disabled: currentPage === 1 }">
            <button class="page-link" @click="changePage(1)">처음</button>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === 1 }">
            <button class="page-link" @click="changePage(currentPage - 1)">이전</button>
          </li>
          <li class="page-item" v-for="page in visiblePages" :key="page" :class="{ active: page === currentPage }">
            <button class="page-link" @click="changePage(page)">{{ page }}</button>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }">
            <button class="page-link" @click="changePage(currentPage + 1)">다음</button>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }">
            <button class="page-link" @click="changePage(totalPages)">끝</button>
          </li>
        </ul>
      </nav>

      <!-- 새 포지션 생성 버튼 -->
      <button @click="handleCreatePosition" class="btn btn-success mt-3">새 포지션 만들기</button>
    </div>
  </section>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: "OpenPosition",
  data() {
    return {
      searchQuery: '',  // 검색어를 위한 v-model 변수
      positions: [],    // DB에서 받아온 포지션 정보
      filteredPositions: [], // 필터링된 포지션 정보
      isLoading: true,  // 로딩 상태
      isSearching: false, // 검색 상태를 나타내는 변수
      itemsPerPage: 10, // 페이지 당 항목 수
      currentPage: 1,   // 현재 페이지
      maxVisiblePages: 10,  // 한 번에 보일 최대 페이지 번호
    };
  },
  computed: {
    ...mapGetters(['isLoggedIn']),  // 로그인 상태를 가져옴
    // 필터링 후 페이지네이션 처리된 포지션 리스트
    paginatedPositions() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredPositions.slice(start, end);
    },
    // 총 페이지 수 계산
    totalPages() {
      return Math.ceil(this.filteredPositions.length / this.itemsPerPage);
    },
    // 현재 페이지 기준으로 보이는 페이지 목록
    visiblePages() {
      const half = Math.floor(this.maxVisiblePages / 2);
      let start = Math.max(1, this.currentPage - half);
      let end = Math.min(this.totalPages, start + this.maxVisiblePages - 1);

      if (end - start < this.maxVisiblePages - 1) {
        start = Math.max(1, end - this.maxVisiblePages + 1);
      }

      return Array.from({ length: end - start + 1 }, (_, i) => start + i);
    }
  },
  methods: {
    goToDetail(positionId) {
      if (positionId) {
        this.$router.push({ name: 'OpenPositionDetail', params: { id: positionId } });
      }
    },
    async fetchPositions() {
      try {
        const response = await fetch('/api/positions');
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        const data = await response.json();
        if (Array.isArray(data)) {
          this.positions = data.reverse(); // 데이터를 역순으로 정렬
          this.filteredPositions = this.positions;
        } else {
          this.positions = [];
        }
      } catch (error) {
        console.error('Error fetching positions:', error);
        this.positions = [];
      } finally {
        this.isLoading = false;
      }
    },
    performSearch() {
      this.isSearching = true;
      if (this.searchQuery) {
        this.filteredPositions = this.positions.filter(position =>
          position.title.toLowerCase().includes(this.searchQuery.toLowerCase())
        );
      } else {
        this.filteredPositions = this.positions;
      }
      this.currentPage = 1; // 검색 시 첫 페이지로 돌아감
    },
    changeItemsPerPage() {
      this.currentPage = 1; // 항목 수 변경 시 첫 페이지로 돌아감
    },
    changePage(page) {
      if (page >= 1 && page <= this.totalPages) {
        this.currentPage = page;
      }
    },
    handleCreatePosition() {
      if (this.isLoggedIn) {
        // 사용자가 로그인된 상태라면 새 포지션 생성 페이지로 이동
        this.$router.push({ name: 'CreatePosition' });
      } else {
        // 로그인이 안 되어 있으면 알림 후 로그인 페이지로 이동
        alert('로그인이 필요합니다. 로그인 페이지로 이동합니다.');
        this.$router.push({ name: 'Login' });
      }
    }
  },
  mounted() {
    this.fetchPositions();
  }
};
</script>

<style scoped>
.custom-table {
  cursor: pointer;
}
</style>
