<template>
    <div>
        <!-- 헤더 -->
        <Header />

        <!-- 페이지의 본문은 router-view로 삽입 -->
        <router-view />

        <!-- 푸터 -->
        <Footer />
    </div>
</template>

<script>
import Header from '@/components/HeaderComponent.vue';
import Footer from '@/components/FooterComponent.vue';

export default {
    name: 'LayoutComponent',
    components: {
        Header,
        Footer,
    },
    mounted() {
        // 페이지 로드 시 로그인 상태 확인
        this.checkLoginStatus();
    },
    methods: {
        checkLoginStatus() {
            // Vuex 액션을 사용하여 로그인 상태 확인
            this.$store.dispatch('checkLoginStatus');
        }
    }
};
</script>
