<template>
  <div class="login-page container">
    <h2>로그인</h2>
    <form @submit.prevent="handleLogin">
      <div class="form-group">
        <label for="email">이메일</label>
        <input type="email" id="email" v-model="email" class="form-control" required placeholder="이메일을 입력하세요" />
      </div>
      <div class="form-group">
        <label for="password">비밀번호</label>
        <input type="password" id="password" v-model="password" class="form-control" required
          placeholder="비밀번호를 입력하세요" />
      </div>
      <div class="form-group">
        <button type="submit" class="btn btn-primary">로그인</button>
      </div>
    </form>
    <p>
      계정이 없으신가요?
      <router-link to="/signup">회원가입</router-link>
    </p>
    <p>
      <router-link to="/forgot-password">비밀번호 찾기</router-link>
    </p>
  </div>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: "LoginHomePage",
  data() {
    return {
      email: '',
      password: ''
    };
  },
  methods: {
    ...mapActions(['login']),
    async handleLogin() {
      try {
        const credentials = {
          email: this.email,  // `email` 필드를 Spring Boot와 일치하도록 사용
          password: this.password
        };

        console.log("Sending credentials:", credentials); // 추가: 데이터 확인용 로그

        // Vuex 스토어의 login 액션 호출
        const response = await this.login(credentials);

        // 로그인 성공 시 메인 페이지로 리디렉션
        if (response.success) {
          this.$router.push('/');
        } else {
          alert(response.message || '로그인에 실패했습니다.');
        }
      } catch (error) {
        // 서버 오류 처리
        alert(error.message || '로그인 실패: 서버에 문제가 발생했습니다.');
      }
    }
  }
};
</script>

<style scoped>
.login-page {
  max-width: 400px;
  margin: 0 auto;
  padding-top: 50px;
}
</style>
