<template>
  <section>
    <div class="container mt-5">
      <!-- 수정 모드인지 새 등록 모드인지에 따라 제목이 달라짐 -->
      <h1 class="text-start mb-5">{{ isEditMode ? '포지션 수정' : '새 포지션 등록' }}</h1>

      <!-- 포지션 등록/수정 폼 -->
      <form @submit.prevent="submitPosition">
        <div class="mb-3">
          <label for="title" class="form-label">포지션명</label>
          <input type="text" id="title" v-model="position.title" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="company" class="form-label">회사명</label>
          <input type="text" id="company" v-model="position.company" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="level" class="form-label">직급</label>
          <input type="text" id="level" v-model="position.level" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="status" class="form-label">상태</label>
          <input type="text" id="status" v-model="position.status" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="date" class="form-label">등록일</label>
          <input type="date" id="date" v-model="position.date" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="description" class="form-label">설명</label>
          <textarea id="description" v-model="position.description" class="form-control" rows="5" required></textarea>
        </div>

        <button type="submit" class="btn btn-primary">{{ isEditMode ? '수정 완료' : '등록' }}</button>
      </form>
    </div>
  </section>
</template>

<script>
export default {
  name: "CreatePosition",
  data() {
    return {
      position: {
        title: '',
        company: '',
        level: '',
        status: '',
        date: '',
        description: ''
      },
      isEditMode: false // 수정 모드 여부
    };
  },
  methods: {
    async fetchPositionDetails(id) {
      try {
        const response = await fetch(`/api/positions/${id}`);
        if (!response.ok) {
          throw new Error('포지션 정보를 불러오는데 실패했습니다.');
        }
        const data = await response.json();
        this.position = data; // 기존 포지션 데이터를 폼에 미리 채움
      } catch (error) {
        console.error('Error fetching position details:', error);
      }
    },
    async submitPosition() {
      try {
        const url = this.isEditMode ? `/api/positions/${this.$route.params.id}` : '/api/positions';
        const method = this.isEditMode ? 'PUT' : 'POST';

        const response = await fetch(url, {
          method: method,
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(this.position)
        });

        if (!response.ok) {
          throw new Error('네트워크 응답에 문제가 발생했습니다.');
        }

        // 글 작성/수정 완료 후 포지션 목록 페이지로 이동
        this.$router.push('/open-position');
      } catch (error) {
        console.error('Error submitting position:', error);
      }
    }
  },
  mounted() {
    const positionId = this.$route.params.id;
    if (positionId) {
      // positionId가 있는 경우 수정 모드로 전환
      this.isEditMode = true;
      this.fetchPositionDetails(positionId); // 기존 포지션 정보를 가져와 폼에 미리 채움
    }
  }
};
</script>

<style scoped>
