<template>
  <div class="container mt-5">
    <div class="card">
      <div class="card-header">
        <h2>{{ position.title }}</h2>
      </div>
      <div class="card-body">
        <ul class="list-group list-group-flush">
          <li class="list-group-item">
            <strong>회사:</strong> {{ position.company }}
          </li>
          <li class="list-group-item">
            <strong>직급:</strong> {{ position.level }}
          </li>
          <li class="list-group-item">
            <strong>상태:</strong> {{ position.status }}
          </li>
          <li class="list-group-item">
            <strong>등록일:</strong> {{ position.date }}
          </li>
          <li class="list-group-item">
            <strong>설명:</strong> {{ position.description }}
          </li>
        </ul>

        <!-- 버튼 그룹 -->
        <div class="d-flex justify-content-start mt-4">
          <!-- 목록으로 돌아가기 버튼 -->
          <router-link to="/open-position" class="btn btn-primary me-2">
            목록으로 돌아가기
          </router-link>

          <!-- 수정 버튼 -->
          <button @click="handleEditPosition" class="btn btn-warning ms-3">
            수정
          </button>

          <!-- 삭제 버튼 -->
          <button @click="handleDeletePosition" class="btn btn-danger ms-3">
            삭제
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: "OpenPositionDetail",
  data() {
    return {
      position: {}, // 선택된 포지션 정보를 저장
    };
  },
  computed: {
    ...mapGetters(['isLoggedIn']), // Vuex 스토어에서 로그인 상태 가져오기
  },
  mounted() {
    // 라우터에서 전달된 ID로 포지션 정보 불러오기
    const positionId = this.$route.params.id;
    this.fetchPositionDetail(positionId);
  },
  methods: {
    // 포지션 상세 정보 불러오기
    async fetchPositionDetail(positionId) {
      try {
        const response = await fetch(`/api/positions/${positionId}`);
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        const data = await response.json();
        this.position = data; // 데이터 할당
      } catch (error) {
        console.error('Error fetching position detail:', error);
      }
    },

    // 포지션 삭제 기능
    async handleDeletePosition() {
      if (!this.isLoggedIn) {
        // 로그인이 안된 상태라면 알림을 띄우고 로그인 페이지로 이동
        alert('로그인이 필요합니다. 로그인 페이지로 이동합니다.');
        this.$router.push({ name: 'Login' });
        return;
      }

      const confirmed = confirm('정말로 이 포지션을 삭제하시겠습니까?');
      if (!confirmed) return; // 사용자가 취소를 누르면 삭제하지 않음

      try {
        const response = await fetch(`/api/positions/${this.$route.params.id}`, {
          method: 'DELETE',
        });

        if (!response.ok) {
          throw new Error('Network response was not ok');
        }

        // 삭제 후 목록 페이지로 이동
        alert('포지션이 삭제되었습니다.');
        this.$router.push('/open-position');
      } catch (error) {
        console.error('Error deleting position:', error);
        alert('포지션 삭제에 실패했습니다.');
      }
    },

    // 포지션 수정 기능
    handleEditPosition() {
      if (!this.isLoggedIn) {
        // 로그인이 안된 상태라면 알림을 띄우고 로그인 페이지로 이동
        alert('로그인이 필요합니다. 로그인 페이지로 이동합니다.');
        this.$router.push({ name: 'Login' });
        return;
      }

      // 로그인된 경우 수정 페이지로 이동
      this.$router.push({ name: 'EditPosition', params: { id: this.position.id } });
    }
  }
};
</script>

