<template>
  <section>
    <div class="container mt-5">
      <h1 class="text-start mb-5">인재 DB</h1>

      <!-- 검색 바 -->
      <div class="input-group search-bar-container mb-3">
        <input type="text" v-model="searchQuery" class="form-control" placeholder="인재 검색">
        <button class="btn btn-primary" @click="performSearch">검색</button>
      </div>

      <!-- 항목 수 선택 드롭다운 -->
      <div class="d-flex justify-content-end mb-3">
        <label for="itemsPerPage" class="me-2">페이지 당 항목 수:</label>
        <select id="itemsPerPage" v-model="itemsPerPage" @change="changeItemsPerPage" class="form-select w-auto">
          <option value="10">10</option>
          <option value="30">30</option>
          <option value="50">50</option>
          <option value="100">100</option>
        </select>
      </div>

      <!-- 인재 목록 테이블 -->
      <table class="table custom-table table-hover">
        <thead>
          <tr>
            <th scope="col">일시</th>
            <th scope="col">포지션</th>
            <th scope="col">성명</th>
            <th scope="col">전화번호</th>
            <th scope="col">이메일</th>
            <th scope="col">학교</th>
            <!--<th scope="col">진행사항</th>-->
            <th scope="col">컨택담당자</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="talent in paginatedTalents" :key="talent.id" @click="goToDetail(talent.id)">
            <td>{{ formatDate(talent.date) }}</td>
            <td>{{ talent.position }}</td>
            <td>{{ talent.name }}</td>
            <td>{{ talent.phone }}</td>
            <td>{{ talent.email }}</td>
            <td>{{ talent.school }}</td>
            <!--<td>{{ talent.status }}</td>-->
            <td>{{ talent.contactPerson }}</td>
          </tr>
        </tbody>
      </table>

      <!-- 페이지네이션 -->
      <nav v-if="totalPages > 1" aria-label="Page navigation">
        <ul class="pagination justify-content-center">
          <li class="page-item" :class="{ disabled: currentPage === 1 }">
            <button class="page-link" @click="changePage(1)">처음</button>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === 1 }">
            <button class="page-link" @click="changePage(currentPage - 1)">이전</button>
          </li>
          <li class="page-item" v-for="page in visiblePages" :key="page" :class="{ active: page === currentPage }">
            <button class="page-link" @click="changePage(page)">{{ page }}</button>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }">
            <button class="page-link" @click="changePage(currentPage + 1)">다음</button>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }">
            <button class="page-link" @click="changePage(totalPages)">끝</button>
          </li>
        </ul>
      </nav>

      <!-- 데이터가 없을 때 보여줄 메시지 -->
      <div v-if="(isSearching && filteredTalents.length === 0)">
        <p>검색 결과가 없습니다. 다른 검색어를 시도해 주세요.</p>
      </div>

      <!-- 로딩 중일 때 보여줄 메시지 -->
      <div v-if="isLoading">
        <p>로딩 중... 잠시 기다려 주세요.</p>
      </div>

      <!-- 새 인재 등록 버튼 -->
      <button @click="handleCreateTalent" class="btn btn-success mt-3">새 인재 등록</button>
    </div>
  </section>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: "TalentArchive",
  data() {
    return {
      searchQuery: '',  // 검색어
      talents: [],  // DB에서 불러온 인재 정보
      filteredTalents: [], // 필터링된 인재 목록
      isLoading: true,  // 로딩 상태
      currentPage: 1,   // 현재 페이지
      itemsPerPage: 10, // 페이지 당 항목 수
      maxVisiblePages: 10, // 보이는 최대 페이지 수
      isSearching: false // 검색 상태
    };
  },
  computed: {
    ...mapGetters(['isLoggedIn']), // 로그인 상태
    // 필터링 후 페이지네이션 처리된 인재 리스트
    paginatedTalents() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredTalents.slice(start, end);
    },
    // 총 페이지 수 계산
    totalPages() {
      return Math.ceil(this.filteredTalents.length / this.itemsPerPage);
    },
    // 현재 페이지 기준으로 보이는 페이지 목록
    visiblePages() {
      const half = Math.floor(this.maxVisiblePages / 2);
      let start = Math.max(1, this.currentPage - half);
      let end = Math.min(this.totalPages, start + this.maxVisiblePages - 1);

      if (end - start < this.maxVisiblePages - 1) {
        start = Math.max(1, end - this.maxVisiblePages + 1);
      }

      return Array.from({ length: end - start + 1 }, (_, i) => start + i);
    }
  },
  methods: {
    goToDetail(talentId) {
      this.$router.push({ name: 'TalentDetail', params: { id: talentId } });
    },
    async fetchTalents() {
      this.isLoading = true;
      try {
        const response = await fetch('/api/talents');
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        const data = await response.json();
        if (Array.isArray(data)) {
          this.talents = data.reverse(); // 데이터를 역순으로 정렬
          this.filteredTalents = this.talents;
        } else {
          this.talents = [];
        }
      } catch (error) {
        console.error('Error fetching talents:', error);
        this.talents = [];
      } finally {
        this.isLoading = false; // 로딩 완료 후 상태 변경
      }
    },
    performSearch() {
      this.isSearching = true;
      if (this.searchQuery) {
        // 이름, 포지션, 학교 등을 기준으로 검색
        this.filteredTalents = this.talents.filter(talent =>
          talent.name.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
          talent.position.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
          talent.school.toLowerCase().includes(this.searchQuery.toLowerCase())
        );
      } else {
        this.filteredTalents = this.talents;
      }
      this.currentPage = 1; // 검색 시 첫 페이지로 돌아감
    },
    changeItemsPerPage() {
      this.currentPage = 1; // 항목 수 변경 시 첫 페이지로 돌아감
    },
    changePage(page) {
      if (page >= 1 && page <= this.totalPages) {
        this.currentPage = page;
      }
    },
    handleCreateTalent() {
      if (this.isLoggedIn) {
        this.$router.push({ name: 'CreateTalent' });
      } else {
        alert('로그인이 필요합니다. 로그인 페이지로 이동합니다.');
        this.$router.push({ name: 'Login' });
      }
    },
    formatDate(date) {
      return new Date(date).toLocaleDateString(); // 날짜 포맷
    }
  },
  mounted() {
    this.fetchTalents();
  }
};
</script>

<style scoped>
.custom-table {
  cursor: pointer;
}
</style>
